body,
html {
  align-items: center;
  background-color: #0c1016;
  display: flex;
  font-family: "Roboto";
  font-size: 10px;
  min-height: 100%;
  justify-content: center;
  margin: 0;
  padding: 0;
  width: 100%;
}

* {
  box-sizing: border-box;
}

h1 {
  font-size: 3.8rem;
  line-height: 1.1em;
  color: #fff;
}

p {
  opacity: 0.6;
  font-size: 3.8rem;
  line-height: 1.7em;
  text-align: justify;
  color: #fff;
}